import React from "react";
import { Store } from "app/redux/Store";
import { getBasename } from "app/utils/baseurl";
const getBaseUrl_bk = () => {
    const getStorevalue = Store.getState();
    const currentPathValue = getStorevalue.globalData.segment || "";
    return (currentPathValue === "/") ? currentPathValue : "/" + currentPathValue;
}
const getBaseUrl = () => {
    const getStorevalue = getBasename();
   // return (getStorevalue?.segment === "/") ? getStorevalue?.segment : "/" + getStorevalue?.segment;
    return (getStorevalue?.segment.startsWith("/")) ? getStorevalue?.segment : "/" + getStorevalue?.segment;
}

export default getBaseUrl;