import React from 'react';
import { Navigate, useLocation, useHref } from 'react-router-dom';
import { LoginCallback } from '@okta/okta-react';

const RedirectRoute = ({ children, ...rest }) => {
    const basename = useHref("");
    const location = useLocation();
    if (basename.startsWith('/')) {
        return <Navigate to="/calist" />;
    } else if (location.pathname.startsWith('/Dev') || location.pathname === '/') {
        return <Navigate to="/calist" />;
    } else if (location.pathname.startsWith('/login/callback')) {
        return <LoginCallback />;
    }
    return children;
};

export default RedirectRoute;