import React, { useEffect, useState } from "react";
import { Provider } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Store } from "./redux/Store";
import Router from "./routes";
import { GlobalCss, MatxSuspense, MatxTheme } from "app/components";
import { ErrorBoundary } from "react-error-boundary";
import Fallback from "./components/Fallback/Fallback";
import { OracleMappingsProvider } from "./contexts/OracleSettingsContext";
import { SettingsProvider } from "./contexts/SettingsContext";
import DragAndDropComponent from "DragAndDrop/DragAndDrop";

const App = () => {
  console.log(" App.jsx is getting called");
  const [hasDropped, setHasDropped] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      performance.mark("FirstContentfulPaint");
    }, 1000);

    console.log("ENV:", process.env.NODE_ENV);
    return () => clearTimeout(timer);
  }, []);

  if (!hasDropped) {
    console.log(
      " Rendering DragAndDropComponent because hasDropped is false"
    );
    return (
      <DragAndDropComponent
        onDropSuccess={(data) => {
          console.log(" Dropping complete:", data);
          setHasDropped(true);
        }}
      />
    );
  }

  return (
    <ErrorBoundary FallbackComponent={Fallback} onReset={() => {}}>
      <Provider store={Store}>
        <SettingsProvider>
          <MatxTheme>
            <GlobalCss />
            <MatxSuspense>
              <OracleMappingsProvider>
                <Router />
              </OracleMappingsProvider>
            </MatxSuspense>
          </MatxTheme>
        </SettingsProvider>
      </Provider>
    </ErrorBoundary>
  );
};

export default App;
